*, *::before, *::after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background: #2c3e50;
}

.main-section {
    background: transparent;
    max-width: 600px;
    width: 90%;
    height: 500px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto;
}

.clock-holder {
    width: 100%;
    background: #fff;
    margin: 30px auto;
}

.stopwatch {
    padding: 60px 0;
    text-align: center;
}