.stopwatch .stopwatch-btn {
    background: #fff;
    margin-top: 30px;
    margin-left: 5px;
    border: none;
    padding: 12px 15px;
    font-size: 16px;
    text-transform: uppercase;
    width: 150px;
    transition: background 1s;
    cursor: pointer;
}

.stopwatch .stopwatch-btn-red {
    border: 2px solid #c85454;;
    color: #c85454;
}

.stopwatch .stopwatch-btn-red:hover {
    background: #c85454;
    color: #fff;
}

.stopwatch .stopwatch-btn-blue {
    border: 2px solid #3636c2;
    color: #3636c2;
}

.stopwatch .stopwatch-btn-blue:hover {
    background: #3636c2;
    color: #fff;
}

.stopwatch .stopwatch-btn-green  {
    border: 2px solid #00aba9;
    color: #00aba9;
}

.stopwatch .stopwatch-btn-green:hover {
    background: #00aba9;
    color: #fff;
}